<template>
  <div class="box">
		<div class="seek-function">
      <div class="left">
        <span @click="goSite">{{this.$store.state.cityName?this.$store.state.cityName: '广州'}}</span>
        <img src="../../public/img/i_city.png">
      </div>
      <div class="right" @click="goSeek">
        <img src="../../public/img/search_ico_01.png">
        <input type="text" placeholder="影片/影院/影人，任你搜">
      </div>
    </div>

		<div class="hot-film">
			<div class="top">
				<h2>正在热映( {{this.$store.state.hotFilmData.length}} )部</h2>
				<img src="../../public/img/i-tmore.png">
			</div>
			<div class="main">
				<div class="cover" v-for="(item, index) in this.$store.getters.hotFilmEight" :key="index" @click="goDetails(item)">
					<img :src="item.img">
					<span class="name">{{item.t}}</span>
					<span class="grade" v-if="item.r > 0">{{item.r}}</span>
				</div>
			</div>
		</div>

		<div class="hot-film">
			<div class="top">
				<h2>即将上映( {{this.$store.state.futureFilmData1.length + this.$store.state.futureFilmData2.length}} )部</h2>
				<img src="../../public/img/i-tmore.png">
			</div>
			<div class="main">
				<div class="cover" v-for="(item, index) in this.$store.getters.futureFilmEight" :key="index">
					<img :src="item.image">
					<span class="name">{{item.title}}</span>
					<span class="grade" v-if="item.r > 0">{{item.r}}</span>
				</div>
			</div>
		</div>

	</div>
</template>

<script>
export default {
	name: "Home",
	data() {
		return {
			
		}
	},
	methods: {
		goSeek() {
			this.$router.push({path: '/Seek'})
		},
		goSite() {
			this.$router.push({path: '/Site'})
		},
		goDetails(item) {
			this.$router.push({path: '/Details'})
			this.$store.dispatch('DetailsCityID', item.id)
		}
	},
	created() {
		this.$store.dispatch('hot')
		this.$store.dispatch('future')
	}
};
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
@import '../../public/sass/mixin';
.box {
	width: 100%;
}

.seek-function {
  display: flex;
  padding: 16px 20px 9px;
  width: calc(100% - 40px);
  height: 42px;
	@include border-bottom(rgb(216, 216, 216));
  background: #f6f6f6;

  .left {
    flex: 0 0 74px;
		height: 100%;
		line-height: 42px;
		text-align: center;

    span {
			display: inline-block;
			font-size: 14px;
			color: #333;
		}
		
		img {
			margin-left: 6px;
			display: inline-block;
			width: 11px;
			height: 6px;
		}
	}
	
	.right {
		position: relative;
		flex: 1;
		padding-left: 30px;
		height: 100%;
		line-height: 42px;
		border: 1px solid rgb(187, 187, 187);
		box-sizing: border-box;
		border-radius: 5px;
		background: #fff;

		img {
			position: absolute;
			left: 5px;
			top: 50%;
			transform: translateY(-50%);
			display: inline-block;
			width: 24px;
			height: 24px;
		}

		input {
			margin-left: 10px;
			height: calc(100% - 2px);
			vertical-align: top;
			border: none;
			outline: none;
			font-size: 16px;
			color: #777;
		}
	}
}

.hot-film {
	padding: 6px 18px;
	width: calc(100% - 36px);
	@include border-bottom(rgb(216, 216, 216));

	.top {
		position: relative;
		margin-bottom: 5px;
		display: flex;
		width: 100%;
		height: 42px;

		h2 {
			flex: 1;
			height: 100%;
			line-height: 42px;
			font-size: 22px;
			font-weight: bold;
			color: #333;
		}

		img {
			flex: 0 0 10px;
			display: inline-block;
			position: absolute;
			top: 50%;
			right: 5px;
			transform: translateY(-40%);
			height: 18px;
		}
	}

	.main {
		display: flex;
		flex-wrap: wrap;
		width: 100%;
	}	

	.main	.cover {
		position: relative;
		display: block;
		margin: 0 3px;
		flex: 0 0 calc(25% - 6px);
		width: calc(25% - 6px);

		img {
			display: inline-block;
			width: 100%;
			height: 75%;
		}

		.name {
			margin-top: 6px;
			margin-bottom: 12px;
			display: block;
			text-align: center;
			white-space: nowrap;
			text-overflow: ellipsis;
			overflow: hidden;
			font-size: 16px;
			color: #333;
		}

		.grade {
			position: absolute;
			right: 4px;
			top: 4px;
			padding: 3px 2px;
			color: #fff;
			background: rgb(101, 157, 14);
		}
	}
	
}


</style>