<template>
  <div class="box" v-if="cityData" >
		<div class="nav">
			<div class="go-back">
				<img src="../../public/img/h_btn_back.png">
			</div>
			<div class="right">
				<div>
					<img src="../../public/img/i_h_collection.png">
				</div>
				<div>
					<img src="../../public/img/i_h_share.png">
				</div>
			</div>
		</div>
		<div class="bg">
			<div class="img">
				<img :src="cityData.image">
			</div>
			<img src="../../public/img/cinemainfo_bg.png">
		</div>
		<div class="movie">
			<div class="img">
				<img :src="cityData.image">
				<img src="../../public/img/viceoplay.png" class="small">
			</div>
			<div class="right">
				<div class="top">
					<h2>{{cityData.titleCn}}</h2>
					<span>{{cityData.titleEn}}</span>
					<p>{{cityData.rating}}</p>
				</div>
				<div class="center">
					<span>{{cityData.runTime}}分钟</span>
					<span class="genre" v-if="cityData.type[0]">{{cityData.type[0]}}</span>
					<span class="genre" v-if="cityData.type[1]">/{{cityData.type[1]}}</span>
					<span>{{cityData.release.date}}{{cityData.release.location}}上映</span>
				</div>
				<div class="bottom">
					<span>我想看</span>
					<span class="active">我要评分</span>
				</div>
			</div>
		</div>
		<div class="text">
			<div class="commonSpecial">
				<img src="../../public/img/i_dot.png">
				<span class="commonSpecial">{{cityData.commonSpecial}}</span>
			</div>
			<span class="shadow">查影讯/购票</span>
		</div>
		<LineFeed/>
		<div class="synopsis">
			<p :class="synopsis? 'active':''">{{cityData.content}}</p>
			<div>
				<img src="../../public/img/i-tmore (1).png" @click="alterSynopsis" :class="synopsis? 'active':''">
			</div>
		</div>
		<LineFeed/>
		<div class="member" v-if="PerformersData">
			<Headline :text="PerformersData[0].persons.length + PerformersData[1].persons.length + '位演职员'"/>
			<div class="item">
				<div class="direct">
					<span class="active">导演</span>
					<img :src="PerformersData[0].persons[0].image">
					<span>{{PerformersData[0].persons[0].name}}</span>
					<span>{{PerformersData[0].persons[0].nameEn}}</span>
				</div>
				
				<div class="actor">
					<span>主要演员</span>
					<div class="main">
						<div v-for="(item, index) in PerformersData[1].persons.slice(0, 2)" :key="index">
							<img :src="item.image">
							<span>{{item.name}}</span>
							<span>{{item.nameEn}}</span>
							<span class="role">饰{{item.personate}}</span>
						</div>
					</div>
					
				</div>
			</div>

		</div>
		<LineFeed/>
		<div class="picture">
			<Headline :text="stillData.length + '张图片'"/>
			<div class="img">
				<img :src="item.image" v-for="(item, index) in stillData.slice(0, 4)" :key="index">
			</div>
		</div>
		<LineFeed/>
		<div class="sift-discuss" v-if="comment">
			<Headline :text="'精彩影评( '+ comment.plus.total +' )'"/>
			<p>{{comment.plus.list[0].title}}</p>
			<span>{{comment.plus.list[0].content}}</span>
			<div class="user">
				<img :src="comment.plus.list[0].headImg">
				<div>
					<span>{{comment.plus.list[0].nickname}}</span>
					<div>
						<p>{{date(comment.plus.list[0].commentDate)}}看过 - 评分</p>
						<span>{{comment.plus.list[0].rating}}</span>
					</div>
				</div>
			</div>
		</div>
		<LineFeed/>
		<div class="small-discuss" v-if="shortReview">
			<div class="nav">
				<p>网友短评( {{shortReview.totalCount}} )</p>
			</div>
			<div class="discuss" v-for="(item, index) in shortReview.cts" :key="index">
				<img :src="item.caimg" v-if="item.caimg">
				<div class="text">
					<div class="top">
						<span>{{item.ca}}</span>
						<div>
							<span>{{date(item.cd)}} - 评</span>
							<p>{{item.cr}}.0</p>
						</div>
					</div>
					<span>{{item.ce}}</span>
					<div class="bottom">
						<div>
							<img src="../../public/img/ico_reply.png">
							<span>回复</span>
						</div>
						<div>
							<img src="../../public/img/ico_praise.png">
							<span>赞</span>
						</div>
					</div>
				</div>
			</div>
		</div>

	</div>
</template>

<script>
import LineFeed from '../components/LineFeed.vue'
import Headline from '../components/Headline.vue'

export default {
	name: "Details",
	data() {
		return {
			synopsis: false,
			//短评数据
			shortReview: ""
		}
	},
	computed: {
		//详情页的电影数据
		cityData() {
			return this.$store.state.DatailsCityData
		},
		//演职员信息
		PerformersData() {
			return this.$store.state.PerformersData
		},
		//剧照信息
		stillData() {
			return this.$store.state.stillData
		},
		//影评信息
		comment() {
			console.log(this.$store.state.commentData)
			return this.$store.state.commentData
		},
	},
	methods: {
		alterSynopsis() {
			this.synopsis = !this.synopsis
		},
		//时间戳 24 小时转换 
		date(date) {
			let timeTrans = new Date(parseInt(date) * 1000)  
      return timeTrans.toLocaleString('chinese',{hour12:false}).replace(/\//g, "-")
		}
	},
	created() {
		this.$store.dispatch('DetailsCity')
		this.$store.dispatch('PerformersData')
		this.$store.dispatch('stillData')
		this.$store.dispatch('comment')
		let url = "/api/Showtime/HotMovieComments.api?pageIndex=1&movieId=" + this.$store.state.DatailsCityDataID
		this.axios.get(url).then(
			response => {
				this.shortReview = response.data.data
				console.log(response.data.data)
			}
		)
	},
	components: {
		LineFeed,
		Headline,
	}
}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
@import '../../public/sass/mixin';
.box {
	margin-top: -54px;
	position: relative;
	z-index: 5;
	width: 100%;
	background: #fff;
}

.nav {
	display: flex;
	justify-content: space-between;
	padding-right: 10px;
	width: calc(100% - 10px);
	height: 54px;

	.go-back {
		flex: 0 0 54px;
		position: relative;
		width: 54px;
		height: 100%;

		img {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: 12px;
		}
	}

	.right {
		flex: 0 0 110px;
		width: 110px;
		height: 100%;

		div {
			position:relative;
			display: inline-block;
			width: 50%;
			height: 100%;
		}

		div img {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: 28px;
		}
	}
}

.bg {
	position: absolute;
	left: 0;
	top: 0;
	z-index: -1;
	width: 100%;

	.img {
		width: 100%;
		height: 130px;
		overflow: hidden;

		img {
			width: 100%;
			filter: blur(10px);
		}
	}
	img {
		width: 100%;
	}
}

.movie {
	display: flex;
	padding-left: 18px;
	margin-top: 10px;

	.img {
		position: relative;
		margin-right: 12px;
		padding: 3px;
		flex: 0 0 126px;
		height: 190px;

		img {
			width: 100%;
			height: 100%;
		}

		img.small {
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			width: 56px;
			height: 56px;
		}
	}

	.right {
		flex: 1;
	}

	.right .top {
		position: relative;
		margin: 6px 18px 18px 0;

		h2 {
			font-size: 22px;
			max-width: 100%;
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
			font-weight: bold;
			color: #fff;
		}

		span {
			display: block;
			margin-top: 10px;
			font-size: 14px;
			color: #fff;
		}

		p {
			position: absolute;
			right: 15px;
			top: 45px;
			width: 34px;
			height: 34px;
			line-height: 34px;
			text-align: center;
			background: rgb(101, 157, 14);
			color: #fff;
		}
	}

	.right .center {
		padding: 10px 0 0;

		span {
			padding-bottom: 10px;
			display: block;
			font-size: 14px;
			font-weight: bold;
			color: #333;
		}

		span.genre {
			display: inline-block;
		}
	}

	.right .bottom {
		margin-top: 5px;

		span {
			display: inline-block;
			margin: 0 5px;
			padding: 10px 15px;
			border: 1px solid rgb(153, 153, 153);
			box-sizing: border-box;
			border-top-left-radius: 50px;
			border-top-right-radius: 50px;
			border-bottom-left-radius: 50px;
			border-bottom-right-radius: 50px;
			font-size: 16px;
			color: rgb(153, 153, 153);
		}

		span.active {
			border: 1px solid #659c0d;
			color: #659c0d;
		}
	}
}

.text {
	width: 100%;
	padding: 10px 0;

	.commonSpecial {
		width: 100%;
		text-align: center;

		img {
			width: 15px;
			height: 15px;
		}
		
		span {
			font-size: 16px;
			font-weight: bold;
			color: #fd8900;
		}
	}

	.shadow {
		position: sticky;
		top: 0px;
		display: block;
		margin: 16px 18px;
		width: calc(100% - 36px);
		height: 48px;
		line-height: 48px;
		text-align: center;
		font-size: 18px;
		font-weight: 700;
		border-top-left-radius: 50px;
		border-top-right-radius: 50px;
		border-bottom-left-radius: 50px;
		border-bottom-right-radius: 50px;
		background: #fd8900;
		color: #fff;
	}
}

.synopsis {
	padding: 12px 18px 0 18px;

	p {
		display: inline-block;
		width: 100%;
		height: 50px;
		line-height: 25px;
		text-overflow: ellipsis;
		// white-space: pre-wrap;
		overflow: hidden;
	}

	p.active {
		height: auto;
	}

	div {
		height: 35px;
		line-height: 35px;
		text-align: center;

		img {
			display: inline-block;
			width: 16px;
		}

		img.active {
			transform: rotate(180deg);
		}
	}
}

.member {
	padding: 4px 18px;
	width: calc(100% - 36px);

	.item {
		display: flex;
		width: 100%;
	}

	.direct {
		margin-right: 10px;
		flex: 0 0 calc(30% - 10px);
		
		img {
			margin: 5px 0 20px;
			width: 100%;
		}

		span {
			display: inline-block;
			width: 100%;
			text-align: center;
			font-size: 14px;
			color: #333;
		}

		span.active {
			text-align: left;
		}
	}

	.actor {
		padding-left: 12px;
		flex: 0 0 calc(70% - 12px);
		width: calc(70% - 12px);
		border-left: 1px solid rgb(216, 216, 216);

		span {
			display: block;
			width: 100%;
			font-size: 14px;
			color: #333;
		}

		.main {
			display: flex;
		}

		.main div {
			flex: 1;
			margin-right: 5px;
		}

		.main div img {
			margin-top: 5px;
			width: 100%;
		}

		.main div span {
			margin-top: 5px;
			display: block;
			width: 100%;
			text-align: center;
			font-size: 14px;
			color: #333;
		}

		.main div span.role {
			font-size: 12px;
		}
	}
}

.picture {
	padding: 4px 18px 20px;
	width: calc(100% - 36px);

	.img {
		display: flex;
		width: 100%;
		height: 80px;

		img {
			flex: 1;
			margin: 0 5px;
			height: 100%;
		}
	}
}

.sift-discuss {
	padding: 4px 18px 20px;
	width: calc(100% - 36px);

	p {
		display: inline-block;
		font-size: 18px;
		font-weight: 700;
		color: #333;
	}

	span {
		margin: 12px 0;
		font-size: 16px;
		line-height: 1.5;
		overflow: hidden;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-line-clamp: 3;
		-webkit-box-orient: vertical;
		color: #333;
	}

	.user {
		display: flex;
		height: 48px;
		
		img {
			margin-right:16px;
			flex: 0 0 48px;
			height: 100%;
			border-radius: 50%;
		} 

		div {
			display: inline-block;
			flex: 1;
		}

		div span {
			margin: 0;
			height: 50%;
			line-height: 24px;
			font-size: 16px;
			color: #777;
		}

		div div {
			height: 50%;
		}

		div div p {
			height: 100%;
			line-height: 24px;
			font-size: 16px;
			font-weight: 400;
			vertical-align: top;
			color: #777;
		}

		div div span {
			padding: 0 2px;
			display: inline-block;
			height: 100%;
			line-height: 24px;
			font-size: 14px;
			color: #fff;
			background: rgb(101, 157, 14);
		}
	}
}

.small-discuss {
	padding: 4px 18px 20px;
	width: calc(100% - 36px);

	.nav {
  	margin-bottom: 10px;
  	height: 46px;
  	width: 100%;
  	background: #fff;

		p {
		width: 100%;
   	 	height: 100%;
    	line-height: 46px;
    	font-size: 20px;
    	font-weight: 700;
    	color: #333;
  		}
	}	

	.discuss {
		display: flex;
		padding-top: 18px;
		width: 100%;
		@include border-bottom(rgb(216, 216, 216));

		img {
			margin-right: 16px;
			flex: 0 0 48px;
			height: 48px;
			border-radius: 50%;
		}
	}

	.discuss .text {
		flex: 1;
		padding: 0;

		.top {
			display: flex;
			margin-bottom: 6px;
			height: 25px;
		}

		.top span {
			flex: 0 0 90px;
			margin-right: 10px;
			max-width: 70px;
			height: 100%;
			overflow: hidden;
			text-overflow: ellipsis;
			line-height: 25px;
			font-size: 14px;
			color: #999;
		}

		.top div {
			flex: 1;
			position: relative;
		}

		.top div p {
			display: inline-block;
			position: absolute;
			top: 50%;
			right: -10px;
			transform: translateY(-50%);
			padding: 3px;
			font-size: 12px;
			color: #fff;
			background: rgb(101, 157, 14);
		}

		span {
			line-height: 1.5;
			font-size: 16px;
			display: -webkit-box;
			-webkit-line-clamp: 4;
			-webkit-box-orient: vertical;
			text-overflow: ellipsis;
			overflow: hidden;
			color: #333;
		}

		.bottom {
			height: 54px;
			line-height: 54px;
			text-align: right;
		}

		.bottom div {
			margin-right: 10px;
			display: inline-block;
		}

		.bottom div img {
			margin-right: 3px;
			width: 24px;
			height: 24px;
		}

		.bottom div span {
			display: inline-block;
			font-size: 14px;
			color: #777;
		}

	}
}
</style>
